.sinWrapper, .sinInput, .sinInput header,.sinInput main, .sinInput footer{
	width: 100%;
	height: 100%;
	padding: 0px;
	margin: 0px;

	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.sinWrapper{
	background-image: url("https://firebasestorage.googleapis.com/v0/b/trello-clone-f948b.appspot.com/o/Internal%2FwhiteBackground.png?alt=media&token=6adf362e-565c-492d-bbf2-dbd41cb2e635");
}

.sinInput{
	width: 70%;
	height: 50%;
}

.sinInput header{
	height: 30%;
}	


.sinInput header h1{
	font-size: 5rem;
	font-weight: bold;
	margin: 0px;
}

.sinInput header h2{
	font-size: 2rem;
	font-weight: bold;
	margin: 0px;
}

.sinInput main{
	width: 80%;
	height: 60%;
}

.sinInput main input{
	width: 90%;
	height: 10%;
	margin: 1% auto;
	font-size: 1.2rem;
	font-weight: bold;
}

.sinInput footer{
	width: 80%;
	height: 10%;
}

.sinInput footer input{
	width: 100%;
	height: 100%;
	font-weight: bold;
	background: #212121;
	font-size: 1.2rem;
	border: 2px solid #000000;
	color: white;
	border-radius: 40px;
	margin: 0px 1%;
	cursor: pointer;
}

.sinInput footer input:hover{
	background: #404040;
}

@media (prefers-color-scheme: dark) {
	.sinWrapper{
		color: white;
		background-image: url("https://firebasestorage.googleapis.com/v0/b/trello-clone-f948b.appspot.com/o/Internal%2FblackBackground.png?alt=media&token=bf16c247-381c-45f4-a35b-e5b8d83181bc");
	}
}

@media only screen and (max-width: 500px) {
	.sinInput{
		width: 100%;
		height: 90%;
	}


	.sinInput header h1{
		font-size: 4rem;
	}


	.sinInput header h2{
		font-size: 1.5rem;
	}
}


@media only screen and (max-height: 450px) {
	.sinInput{
		height: 90%;
	}

	.sinInput header{
		height: 20%;
	}

	.sinInput header h1{
		font-size: 2rem;
	}

	.sinInput header h2{
		font-size: 1rem;
	}


	.sinInput main{
		height: 70%;
	}


	.sinInput footer{
		height: 10%;
	}
}